<template>
  <div>
    <h3 class='title'>{{cityName}}疫情数据</h3>
      <div class="city" id='city'></div>
  </div>
</template>

<script>
import api from '../api'
export default {
    props:{
        cityName:{
            type:[String],
            default:'北京',
        }
    },
    mounted(){
        api.getGuoneiNcov({
            city:this.cityName
        }).then(res=>{
            console.log(res.data);
            var citys=[];
            for(let i=0;i<res.data.retdata.subList.length;i++){
                var temp={
                    // 接口数据给的是 南宁 开封 而我们echarts地图要渲染所需要的字段是南宁市和开封市...

                    name:res.data.retdata.subList[i].city+'市',
                    value:res.data.retdata.subList[i].curConfirm,
                }
                citys.push(temp)
            
            }
            this.$charts.cityMap('city',citys,this.cityName)

        }).catch(error=>{
            console.log(error);
        })
    
    }
}
</script>

<style scoped>
.title{
    width: 100%;
    height: 50px;
    line-height: 50px;
    background: #2828ff;
    color: whitesmoke;
    text-align: center;
    font-size: 20px;
}
    .city{
        width: 375px;
        height: 400px;
    }
</style>